@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800;900&display=swap);
* {
  margin: 0; padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600; line-height: 1.6 !important;
}

body {
  width: 100%; height: 100%;
  background-color: crimson !important;
  
}

* ::selection {
  color: black; background: chocolate;
}
header {
    height: 120px;
    background: #F7F7F7;
}
header .container,  header .row {
    height: 100%;
}

header .col-md-12 {
    display: flex; justify-content: center; align-items: center;
}

header img {
    width: 150px; height: 110px; object-fit: cover; object-position: top;
    border-radius: 50%;
}
header p {
    margin-bottom: 0; margin-left: auto;
    font-size: 1.75rem; border-bottom: 1px solid crimson;
}

@media (max-width: 575.98px) {
    header p {
        font-size: 1.35rem; margin-left: 2rem;
    }
}

footer {
    height: 80px;
    background: #F7F7F7;
    display: flex !important; justify-content: center; align-items: center;
}
footer p {
    margin: 0; color: crimson;
}
.btn {
    background: inherit;
    color: burlywood;
    border: none !important;
}
.btn:hover {
    background: inherit;
}
.btn:hover:first-of-type {
    color: #3d6ad6;
}
.btn:hover:last-of-type {
    color: crimson;
}
.btn:focus:first-of-type {
    color: #3d6ad6; 
    background: inherit;
    border: none !important; box-shadow: none !important;
}
.btn:focus:last-of-type {
    color: crimson; outline: none !important;
    background: inherit;
    border: none !important; box-shadow: none !important;
}
a:focus {
    outline: none !important;
}
.btn:active {
    border: none !important; box-shadow: none !important;
     background: inherit;
}
.card-text {
    margin-bottom: .25rem;
}
.nav-tabs {
    margin-bottom: 1rem; position: -webkit-sticky; position: sticky; top: 0; z-index: 10;
    background-color: black; border-bottom: none;
    display: flex; justify-content: center; padding: 10px;
}
.nav-tabs .nav-link {
    border: none;
}
.nav-tabs .nav-link.active {
    background-color: inherit;
    border-bottom: 2px solid #3d6ad6;
}
.nav-tabs .nav-link, .nav-tabs .nav-link.active {
    color: crimson;
}

.spinner-grow {
display: flex; justify-content: center; align-content: center;
margin: 1rem auto;
}
.edited-badge-pill {
    position: absolute; top: 5px;
}

